import React, { useEffect, useState } from "react";
import { Row, Col, Divider, Input, Button, Form } from "antd";
import { useLocation, Route, useHistory } from "react-router-dom";
import "./index.style.less";
import RuleGather from "./components/RuleGather/RuleGather";

const AddRequestBody = () => {
  let [url, setUrl] = useState("");
  let [webname, setWebName] = useState("");

  let [arg, setArg] = useState("");
  let [reqBody, setReqBody] = useState("");
  let [reqCookie, setReqCookie] = useState("");

  const history = useHistory();

  const location: {
    state: object;
  } = useLocation();
  interface State {
    url?: any;
    webname?: any;
  }

  useEffect(() => {
    const state: State = location.state;
    if (location.state && state.url && state.webname) {
      setUrl(state.url);
      setWebName(state.webname);
    }
  }, []);

  //表单提交
  const submitForm = () => {
    console.log(arg, reqBody, reqCookie);
    history.push("/AddRequestBody/addGatherRule");
  };
  //重置表单
  const resetForm = () => {
    setArg("");
    setReqBody("");
    setReqCookie("");
  };

  const onFinish = (values: any) => {
    console.log("Success:", values);
  };

  const testHtml = `<div>pdjj</div> `;

  return (
    <div>
      <Row>
        <Col span={24}>要采集网站名称：{webname}</Col>
      </Row>
      <Row>
        <Col span={24}>要采集网站地址：{url}</Col>
      </Row>
      <Divider />
      <Route
        path="/AddRequestBody"
        exact
        render={() => {
          return (
            <div>
              <Row>
                <Col span={24}>请填写请求包头信息:</Col>
                <Col>
                  <Input
                    value={reqBody}
                    onInput={(e: any) => {
                      setReqBody(e.target.value);
                    }}
                  />
                </Col>
              </Row>
              <Divider />
              <Row>
                <Col span={24}>请填写请求参数类型:</Col>
                <Col>
                  <Input
                    value={arg}
                    onInput={(e: any) => {
                      setArg(e.target.value);
                    }}
                  />
                </Col>
              </Row>
              <Divider />
              <Row>
                <Col span={24}>请填写cookie信息:</Col>
                <Col>
                  <Input
                    value={reqCookie}
                    onChange={(e: any) => {
                      setReqCookie(e.target.value);
                    }}
                  />
                </Col>
              </Row>
              <Divider />
              <Row>
                <Col style={{ display: "flex", justifyContent: "center" }}>
                  <Button onClick={submitForm}>提交</Button>
                  <Button onClick={resetForm}>重置</Button>
                </Col>
              </Row>
            </div>
          );
        }}
      />
      <Route
        exact
        path="/AddRequestBody/addGatherRule"
        render={() => {
          return (
            <div>
              <Form
                name="basic"
                initialValues={{ remember: true }}
                onFinish={onFinish}
              >
                <Row>
                  <Col span={24}>模板地址:</Col>
                </Row>
                <Form.Item
                  name="webUrl"
                  rules={[
                    {
                      required: true,
                      type: "url",
                      message: "Please input your a url !",
                    },
                  ]}
                >
                  <Input allowClear />
                </Form.Item>
                <Row>
                  <Col span={24}>下一页模板地址:</Col>
                </Row>
                <Form.Item
                  name="nextUrl"
                  rules={[
                    {
                      required: true,
                      type: "url",
                      message: "Please input your a url",
                    },
                  ]}
                >
                  <Input allowClear maxLength={100} />
                </Form.Item>
                <Row>
                  <Col>
                    <Form.Item>
                      <Button type="primary" htmlType="submit">
                        确定查询
                      </Button>
                    </Form.Item>
                  </Col>
                </Row>
              </Form>

              <Row>
                <Col span={24}>
                  <RuleGather webHtml={testHtml} />
                </Col>
              </Row>
            </div>
          );
        }}
      />
    </div>
  );
};
export default AddRequestBody;
