import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { Form, Input, InputNumber, Button, Radio } from "antd";
const layout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 16 },
};

const validateMessages = {
  required: "${label} is required!",
  types: {
    number: "${label} is not a valid number!",
    url: "${label} is not a valid url!",
  },
};

interface values {
  websiteUrl: string;
  websiteName: string;
}

const AddGather = () => {
  const history = useHistory();
  const onFinish = (values: values) => {
    console.log("values", values);
    history.push({
      pathname: "/AddRequestBody",
      state: {
        url: values.websiteUrl,
        webname: values.websiteName,
      },
    });
  };

  return (
    <Form
      {...layout}
      labelAlign="right"
      name="nest-messages"
      onFinish={onFinish}
      validateMessages={validateMessages}
    >
      <Form.Item
        name={["websiteName"]}
        label="网站名称"
        rules={[{ required: true }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name={["websiteUrl"]}
        label="搜索网址"
        rules={[{ type: "url", required: true }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name={["level"]}
        label="数据层级"
        rules={[{ type: "number", required: true }]}
      >
        <InputNumber />
      </Form.Item>
      <Form.Item name={["code"]} label="编码" rules={[{ required: true }]}>
        <Input />
      </Form.Item>
      <Form.Item
        name={["websiteSign"]}
        label="网站数据标识"
        rules={[{ required: true }]}
      >
        <Input.TextArea />
      </Form.Item>
      <Form.Item
        name={["httpTimes"]}
        label="请求次数/分钟"
        rules={[{ required: true }]}
      >
        <Input value="60" />
      </Form.Item>
      <Form.Item
        name={["radio-group"]}
        label="数据请求方式"
        rules={[{ required: true }]}
      >
        <Radio.Group>
          <Radio value="get">get</Radio>
          <Radio value="post">post</Radio>
        </Radio.Group>
      </Form.Item>
      <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
};

export default AddGather;
