import React from "react";
import { UnorderedListOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import "./index.style.less";

const mapStateToProps = (state: any) => {
  console.log("state.IsShow", state);
  return {
    IsShow: state.IsShow,
  };
};

const mapActionToProps = (dispatch: any, ownProps: any) => {
  return {
    changeAsiderShowState: (arg: boolean) => {
      dispatch({ type: "CHANGE_MEMU", showState: !arg });
    },
  };
};

const ShowAsiderButton = (props: any) => {
  console.log("props", props);
  return (
    <UnorderedListOutlined
      onClick={() => {
        console.log("props.isShow", props.IsShow);
        console.log("props", props);
        props.changeAsiderShowState(props.IsShow);
      }}
      className="showasiderbutton"
    />
  );
};
const ConnectShowAsiderButton = connect(
  mapStateToProps,
  mapActionToProps
)(ShowAsiderButton);
export default ConnectShowAsiderButton;
