import React, { useState, useEffect } from "react";
import { Table, Button } from "antd";
import { useHistory, NavLink } from "react-router-dom";
import {
  AppstoreAddOutlined,
  EditOutlined,
  DeleteOutlined,
} from "@ant-design/icons";
const columns = [
  {
    title: "网站名称",
    dataIndex: "name",
  },
  {
    title: "网址",
    dataIndex: "url",
  },
  {
    title: "请求方式",
    dataIndex: "httpmethods",
  },
  {
    title: "编码类型",
    dataIndex: "enchartType",
  },
  {
    title: "数据类别",
    dataIndex: "dataType",
  },
  {
    title: "编码",
    dataIndex: "code",
  },
  {
    title: "数据量",
    dataIndex: "dataCount",
    render: (
      text: any,
      record: {
        key: number;
        dataCount: string;
        url: string;
      }
    ) => {
      return (
        <NavLink
          to={{
            pathname: "/DataCollection",
            state: {
              url: record.url,
            },
          }}
        >
          {record.dataCount}
        </NavLink>
      );
    },
  },
  {
    title: "状态",
    dataIndex: "state",
  },
];

const data: any = [];
for (let i = 0; i < 46; i++) {
  data.push({
    key: i,
    name: `Edward King ${i}`,
    url: 32,
    address: `London, Park Lane no. ${i}`,
    httpmethods: "post",
    enchartType: "编码类型",
    dataType: "数据类别",
    code: "编码",
    dataCount: "数据量",
    state: "状态",
  });
}
const GatherList = () => {
  let [selectedRowKeys, setSelectedRowKeys] = useState([]);
  let [loading, SetLoading] = useState(false);

  const history = useHistory();

  useEffect(() => {
    
  },[]);

  //添加采集
  const addGatherUrl = () => {
    console.log("add");
    history.push("/AddGather");
  };

  const start = () => {
    SetLoading(true);
    // ajax request after empty completing
    setTimeout(() => {
      SetLoading(false);
      setSelectedRowKeys([]);
    }, 1000);
  };

  const onSelectChange = (selectedRowKeys: any) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    setSelectedRowKeys(selectedRowKeys);
  };
  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };
  return (
    <div style={{ width: "100%" }}>
      <div>
        <Button
          icon={<AppstoreAddOutlined />}
          onClick={addGatherUrl}
          loading={loading}
        >
          添加
        </Button>
        <Button icon={<EditOutlined />} onClick={start} loading={loading}>
          编辑
        </Button>
        <Button icon={<DeleteOutlined />} onClick={start} loading={loading}>
          删除
        </Button>
      </div>
      <Table
        pagination={{ position: ["bottomCenter"] }}
        rowSelection={rowSelection}
        columns={columns}
        dataSource={data}
      />
    </div>
  );
};

export default GatherList;
