import React, { useEffect } from "react";
import { useLocation, useHistory, Route, Redirect } from "react-router-dom";
import Header from "@/components/Header";
import Asider from "@/components/Asider";
import Footer from "@/components/Footer";
import Contents from "@/components/Contents";
import Crumbs from "@/components/Crumbs";
import { Row, Col } from "antd";
import { connect } from "react-redux";
import axios from "axios";
import api from "@/api";
import "./App.less";

const mapStateToProps = (state: any) => {
  // console.log("state.IsShow", state.IsShow);
  return {
    isShowSiderBar: state.IsShow,
  };
};

function App(props: { isShowSiderBar: boolean }) {
  const history = useHistory();
  const location = useLocation();
  useEffect(() => {
    console.log("props.isShowSiderBar", props.isShowSiderBar);
  });
  useEffect(() => {
    // axios.get("http://localhost:4800/test").then((data) => {
    //   console.log("data", data);
    // });
    //rem布局单位计算
    function setRem() {
      const initWidth = 960;
      const scaleWidth = document.documentElement.clientWidth;
      document.documentElement.style.fontSize =
        scaleWidth > initWidth
          ? 16 + "px"
          : scaleWidth < 576
          ? 16 + "px"
          : (16 / 960) * scaleWidth + "px";
    }
    document.addEventListener("DOMContentLoaded", setRem, false);
    window.onresize = function () {
      setRem();
    };
  });

  return (
    <Route
      children={() => {
        return (
          <div className="App">
            {/**头部*/}
            <Header />
            <Asider isShow={props.isShowSiderBar} isMaxScreen={false} />
            {/** 内容区*/}
            <Row wrap={false}>
              <Col flex="200px" className="asider-none">
                <Asider isShow={!props.isShowSiderBar} isMaxScreen={true} />
              </Col>

              <Col flex="auto" className="common-content">
                <div className="common-crumbs">
                  <Crumbs />
                  <Contents />
                </div>
              </Col>
            </Row>
            {/**尾部 */}
            <Row>
              <Col span="24" className="footer">
                <Footer />
              </Col>
            </Row>
          </div>
        );
      }}
    />
  );
}

export default connect(mapStateToProps)(App);
